import axios from "axios";
import { toast } from "react-toastify";
import { Alert } from "react-bootstrap";
import { Link } from "react-router-dom";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import AdminNavs from "../../components/Admin/AdminNavs";
import { listProducts } from "../../actions/productAction";

const SuppliersDetail = ({ match }) => {
  const agent = match.params.name;
  const [givePay, setgivePay] = useState(0);
  const [isLoad, setisload] = useState(false);
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);
  const { products, loading, error } = useSelector(
    (state) => state.listProducts
  );

  const suppliers = !loading && products.filter((p) => p.supplier === agent);
  const sumTotalPay =
    suppliers &&
    suppliers.map((x) => x.totalPay).reduce((prev, curr) => prev + curr, 0);
  const sumGivePay =
    suppliers &&
    suppliers.map((x) => x.givePay).reduce((prev, curr) => prev + curr, 0);
  const remaining = sumTotalPay - sumGivePay;

  let responce;

  const submitPay = async (e) => {
    e.preventDefault();

    if (givePay > remaining) {
      toast.error("Your amount isLoad greater then remaining");
    } else {
      try {
        responce = "request";
        await suppliers.map((x) => {
          axios.put(
            `http://localhost:8000/api/products/update/supplier/${x._id}`,
            { givePay },
            {
              headers: {
                "Content-Type": "Application/json",
                authToken: `${user.token}`,
              },
            }
          );
          responce = "success";
          setisload(true);
        });
        setgivePay(0);
      } catch (error) {
        toast.error(error);
      }
    }
    console.error(responce, "here");
  };

  useEffect(() => {
    dispatch(listProducts());
    setisload(false);
  }, [dispatch, isLoad]);

  return (
    <>
      <AdminNavs />

      <section class="text-gray-600 py-10 body-font bg-gray-50">
        <div class="container px-5  mx-auto flex flex-wrap">
          <div class="flex relative pt-10 pb-20 sm:items-center md:w-2/3 mx-auto">
            <div class="h-full w-6 absolute inset-0 flex items-center justify-center">
              <div class="h-full w-1 bg-gray-200 pointer-events-none"></div>
            </div>
            <div class="flex-shrink-0 w-6 h-6 rounded-full mt-10 sm:mt-0 inline-flex items-center justify-center bg-indigo-500 text-white relative z-10 title-font font-medium text-sm">
              1
            </div>
            <div class="flex-grow md:pl-8 pl-6 flex sm:items-center items-start flex-col sm:flex-row">
              <div class="flex-grow sm:pl-6 mt-6 sm:mt-0">
                <h2 class="font-medium title-font text-gray-900 mb-1 text-xl">
                  {isLoad && <p>loading... </p>}
                  {suppliers.length === 0
                    ? " We didnt take any product of this supplier "
                    : ` Total Product : ${suppliers && suppliers.length}`}
                </h2>
              </div>
            </div>
          </div>

          {suppliers.length !== 0 && (
            <>
              <div class="flex relative pb-20 sm:items-center md:w-2/3 mx-auto">
                <div class="h-full w-6 absolute inset-0 flex items-center justify-center">
                  <div class="h-full w-1 bg-gray-200 pointer-events-none"></div>
                </div>
                <div class="flex-shrink-0 w-6 h-6 rounded-full mt-10 sm:mt-0 inline-flex items-center justify-center bg-indigo-500 text-white relative z-10 title-font font-medium text-sm">
                  2
                </div>
                <div class="flex-grow md:pl-8 pl-6 flex sm:items-center items-start flex-col sm:flex-row">
                  <div class="flex-grow sm:pl-6 mt-6 sm:mt-0">
                    <h2 class="font-medium title-font text-gray-900 mb-1 text-xl">
                      total Price: {sumTotalPay} PKR
                    </h2>
                  </div>
                </div>
              </div>
              <div class="flex relative pb-20 sm:items-center md:w-2/3 mx-auto">
                <div class="h-full w-6 absolute inset-0 flex items-center justify-center">
                  <div class="h-full w-1 bg-gray-200 pointer-events-none"></div>
                </div>
                <div class="flex-shrink-0 w-6 h-6 rounded-full mt-10 sm:mt-0 inline-flex items-center justify-center bg-indigo-500 text-white relative z-10 title-font font-medium text-sm">
                  3
                </div>
                <div class="flex-grow md:pl-8 pl-6 flex sm:items-center items-start flex-col sm:flex-row">
                  <div class="flex-grow sm:pl-6 mt-6 sm:mt-0">
                    <h2 class="font-medium title-font text-gray-900 mb-1 text-xl">
                      Given Amount : {sumGivePay} PKR
                    </h2>
                  </div>
                </div>
              </div>
              <div class="flex relative pb-10 sm:items-center md:w-2/3 mx-auto">
                <div class="h-full w-6 absolute inset-0 flex items-center justify-center">
                  <div class="h-full w-1 bg-gray-200 pointer-events-none"></div>
                </div>
                <div class="flex-shrink-0 w-6 h-6 rounded-full mt-10 sm:mt-0 inline-flex items-center justify-center bg-indigo-500 text-white relative z-10 title-font font-medium text-sm">
                  4
                </div>
                <div class="flex-grow md:pl-8 pl-6 flex sm:items-center items-start flex-col sm:flex-row">
                  <div class="flex-grow sm:pl-6 mt-6 sm:mt-0">
                    <h2 class="font-medium title-font text-gray-900 mb-1 text-xl">
                      remaining : {remaining} PKR
                    </h2>
                  </div>
                </div>
              </div>

              <div class="flex relative pb-10 sm:items-center md:w-2/3 mx-auto">
                <div class="h-full w-6 absolute inset-0 flex items-center justify-center">
                  <div class="h-full w-1 bg-gray-200 pointer-events-none"></div>
                </div>
                <div class="flex-shrink-0 w-6 h-6 rounded-full mt-10 sm:mt-0 inline-flex items-center justify-center bg-indigo-500 text-white relative z-10 title-font font-medium text-sm">
                  5
                </div>
                <div class="flex-grow md:pl-8 pl-6 flex sm:items-center items-start flex-col sm:flex-row">
                  <div class="flex-grow sm:pl-6 mt-6 sm:mt-0">
                    <h2 class="font-medium title-font text-gray-900 mb-1 text-xl">
                      Pay Remaining :{" "}
                      <form onSubmit={submitPay}>
                        <div class="relative mb-4">
                          <input
                            type="number"
                            required
                            value={givePay}
                            name="givePay"
                            onChange={(e) => setgivePay(e.target.value)}
                            class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                          />
                        </div>
                      </form>
                    </h2>
                  </div>
                </div>
              </div>

              <div class="flex relative pb-10 sm:items-center md:w-2/3 mx-auto">
                <div class="h-full w-6 absolute inset-0 flex items-center justify-center">
                  <div class="h-full w-1 bg-gray-200 pointer-events-none"></div>
                </div>
                <div class="flex-shrink-0 w-6 h-6 rounded-full mt-10 sm:mt-0 inline-flex items-center justify-center bg-indigo-500 text-white relative z-10 title-font font-medium text-sm">
                  6
                </div>
                <div class="flex-grow md:pl-8 pl-6 flex sm:items-center items-start flex-col sm:flex-row">
                  <div class="flex-grow sm:pl-6 mt-6 sm:mt-0">
                    <h2 class="font-medium title-font text-gray-900 mb-1 text-xl">
                      Products :{" "}
                      {suppliers &&
                        suppliers.map((x) => (
                          <>
                            <div class="text-blue-700" key={x._id}>
                              <strong>
                                <Link to={`/product/${x._id}`}> {x.name} </Link>,
                              </strong>
                            </div>
                          </>
                        ))}
                    </h2>
                  </div>
                </div>
              </div>
            </>
          )}
        </div>
      </section>
    </>
  );
};

export default SuppliersDetail;
